import { Component, OnInit, AfterViewInit, ElementRef, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import Tether from 'tether';
import Shepherd from 'tether-shepherd';


const DATA = [
  {
    id: 0,
    name: 'dong',
    cat: 'bot'
  },
  {
    id: 1,
    name: 'dong 2',
    cat: 'me'
  },
  {
    id: 2,
    name: 'dong 3',
    cat: 'bot'
  },
  {
    id: 3,
    name: 'hung',
    cat: 'me'
  },
  {
    id: 4,
    name: 'hai',
    cat: 'partner'
  },
  {
    id: 5,
    name: 'long 3',
    cat: 'me'
  }
];

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css']
})
export class ListComponent implements OnInit, AfterViewInit {
  @ViewChild('scrollMe') private myScrollContainer: ElementRef;

  items: any;
  id: number;
  select: string;
  a: number;

  constructor(
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.id = 1;
    this.select = 'all';
    this.items = DATA;
      this.scrollToBottom();
    this.a = 1;
    this.route.params.subscribe((data: any) => {
      console.log(data);
    });

    let tour;
    tour = new Shepherd.Tour({
      defaults: {
        classes: 'shepherd-element shepherd-open shepherd-theme-arrows',
        showCancelLink: true
      }
    });
    tour.addStep('welcome', {
      text: ['Shepherd is a javascript library for guiding users through your app. It uses <a href="http://github.hubspot.com/tether/">Tether</a>, another open source library, to position all of its steps.', 'Tether makes sure your steps never end up off screen or cropped by an overflow. Try resizing your browser to see what we mean.'],
      attachTo: '.hero-welcome bottom',
      classes: 'shepherd shepherd-open shepherd-theme-arrows shepherd-transparent-text',
      buttons: [
        {
          text: 'Exit',
          classes: 'shepherd-button-secondary',
          action: tour.cancel
        }, {
          text: 'Next',
          action: tour.next,
          classes: 'shepherd-button-example-primary'
        }
      ]
    });
    tour.addStep('including', {
      title: 'Including',
      text: 'Including Shepherd is easy! Just include shepherd.js, and a Shepherd theme file.',
      attachTo: '.hero-including bottom',
      buttons: [
        {
          text: 'Back',
          classes: 'shepherd-button-secondary',
          action: tour.back
        }, {
          text: 'Next',
          action: tour.next
        }
      ]
    });
    tour.addStep('example', {
      title: 'Example Shepherd',
      text: 'Creating a Shepherd is easy too! Just create Shepherd and add as many steps as you want. Check out the <a href="http://github.hubspot.com/shepherd">documentation</a> to learn more.',
      attachTo: '.hero-example bottom',
      buttons: [
        {
          text: 'Back',
          classes: 'shepherd-button-secondary',
          action: tour.back
        }, {
          text: 'Next',
          action: tour.next
        }
      ]
    });
    tour.addStep('followup', {
      title: 'Learn more',
      text: 'Star Shepherd on Github so you remember it for your next project',
      attachTo: '.hero-followup bottom',
      buttons: [
        {
          text: 'Back',
          classes: 'shepherd-button-secondary',
          action: tour.back
        }, {
          text: 'Done',
          action: tour.next
        }
      ]
    });
    tour.start();

  }

  view(id: number) {
    this.id = id;
  }

  cat(cat: any) {
    this.items = DATA.filter((data: any) => {
      if (cat === 'all')
      return DATA;
      else
      return data.cat == cat;
    });
  }

  ngAfterViewInit() {        
      this.scrollToBottom();        
  } 

  scrollToBottom(): void {
      try {
          this.myScrollContainer.nativeElement.scrollTop = this.myScrollContainer.nativeElement.scrollHeight;
      } catch(err) { }                 
  }

  scrolls(event: any) {
    const add = {
      id: 5,
      name: 'add 1',
      cat: 'me'
    };
    if (this.myScrollContainer.nativeElement.scrollTop === 0) {
      this.a += 1;
      this.items.unshift(add);
      console.log(this.a);
    }
  }

}



